@import url(https://fonts.googleapis.com/css?family=Oxygen:300,700|Source+Sans+Pro:300,400,700);html{font-family:"Source Sans Pro",sans-serif;font-weight:300;font-size:18px;color:#3F3F3F;background-color:white;overflow-y:scroll}@media screen and (max-width: 600px){html{font-size:16px}}body{margin:0 auto;max-width:100vw;min-height:100vh;display:flex;flex-direction:column;align-items:center}ul,li{list-style-type:none}h1,h2,h3,h4{font-weight:300}a,a:hover,a:active,a:visited{color:#E91616}ul{padding-left:0.6rem}.bold{font-weight:700}.red{color:#E91616}.small{font-size:0.6rem}.tip{position:absolute;display:none;top:110%;right:0;padding:0.4rem;font-size:0.7rem;text-align:center;background-color:#3F3F3F;color:#dcdcdc;border-radius:2px}.tip:before{display:block;position:absolute;content:"";top:-5px;right:10px;width:10px;height:10px;transform:rotate(45deg);background-color:#3F3F3F}header{align-self:stretch;height:52px;background-color:#dcdcdc;display:flex;flex-direction:row;align-items:center;justify-content:space-between}header #logo{align-self:flex-start;height:52px;width:52px;flex:none;background-color:#3F3F3F;display:flex;align-items:center;justify-content:center}header #title{flex:2 1;padding:0 0.5rem;display:flex;flex-direction:row;justify-content:space-around}header #title h1{font-weight:300;font-size:1.5rem;margin:0;line-height:1}header #title .subtitle{display:block;font-size:0.8rem;line-height:1;margin-top:-0.2em;text-align:right}header #menu{flex:1 0;max-width:120px;display:flex;flex-direction:row;justify-content:space-around;align-items:center}header #menu .icon{position:relative;padding:0 0.3em;cursor:pointer}header #menu .icon:hover .tip{display:block}header #menu .icon:hover svg use g{fill:#8c8c8c}@media screen and (max-width: 600px){header #menu .icon svg{width:1.5rem}}.content{flex:1 1;max-width:100vw;padding:0 1rem}.content #switch{text-align:center;margin-top:1em;font-size:0.8rem;font-weight:400}.content #switch span{display:inline-block;padding:0 0.6em 1px 0.6em;border:1px solid #3F3F3F;color:#3F3F3F;cursor:pointer}.content #switch span.dl{border-radius:4px 0 0 4px}.content #switch span.fd{border-radius:0 4px 4px 0}.content #switch span.selected{background-color:#3F3F3F;color:#dcdcdc;cursor:default}.content #switch span:not(.selected):hover{background-color:#E91616;color:#dcdcdc}.content .group{background-color:#dcdcdc;padding:1em;margin-bottom:1em}.content .group h4{margin:0 0 0.4em 0;font-weight:700;font-size:0.8rem}.content .group ul.dls{padding-left:0.8em}.content .group ul.dls li{position:relative;margin-bottom:0.5em}.content .group ul.dls li.new:before{position:absolute;left:-9px;top:9px;background:#E91616;width:6px;height:6px;border-radius:3px;content:''}.content ul.feeds li{background-color:#dcdcdc;padding:1em;margin-bottom:1em;overflow:hidden;white-space:nowrap;text-overflow:clip}@media screen and (min-width: 800px){.content{max-width:800px}}.content #notif{color:#a9a9a9;overflow:hidden}.content #notif p{padding:1em;transition:background-color 1s}.content #notif p.flash{background-color:#eee}.slide-fade-enter-active,.slide-fade-leave-active{max-height:200px;transition:all .5s}.slide-fade-enter,.slide-fade-leave-to{max-height:0;transform:scale(0, 0);opacity:0}.just-fade-enter-active,.just-fade-leave-active{transition:all .25s}.just-fade-enter,.just-fade-leave-to{opacity:0}[v-cloak]{display:none}
